import React, { useEffect, useState } from "react";

import axios from "axios";

export default function Tree({ setCategory }) {
    const [data, setData] = useState([]);
    // const [data, setData] = useState({});
    useEffect(() => {
        const getResponse = async () => {
            const { data: response } = await axios("http://tresenew.climatizacion.cl/categorias/json.asp?id=1");
            setData(response)
        }
        getResponse();
    }, [])

    const getCategory = async (node) => {
        let id = node.replace("node_", "")
        const {data: response} = await axios.get(`http://tresenew.climatizacion.cl/categorias/json.asp?id=${id}`).catch(err => console.log(err));
        setData(response)
        console.log(response)
    }

    const handleNode = (node) => {
        setCategory(node);
    }
    return (
        <div className="text-black text-sm w-full ">
           { data.length > 0 && data.map((cat) => (
                <div className="p-1 border border-collapse" key={cat.attr.id}>
                    
                        <div className="cursor-pointer w-full" >
                         
                         {cat.metadata.hijos == 0  ? (
                                    <div className="flex justify-start items">
                                    <input onClick={() => handleNode(cat.attr.id)} type="radio" name="radioBtn" className="" id={cat.attr.id} />
                                    <label htmlFor={cat.attr.id}>{cat.data}</label>
                                    
                                    </div>
                                ) :  <label className="text-black" > <label className="flex cursor-pointer" onClick={() => getCategory(cat.attr.id)}>{cat.data}</label> </label>}
                    </div>
                     
                    
                </div>
            ))}
        </div>
    );
}
