import React, { useState } from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import ModalMUI from "@mui/material/Modal";
import Tree from "../components/Tree";
import axios from "axios";

const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 600,
    height: 500,
    boxShadow: 24,
    p: 4,
};


export default function Modal({ title, opencat, handleCloseCat, selectionModel }) {
    const [category, setCategory] = useState("");

    const sendCategory = async () => {
        handleCloseCat();
        await axios.post("http://18.212.211.52:3000/api/send-category", {
            sku: selectionModel,
            category,
        })
    }
    return (
        <ModalMUI
            open={opencat}
            onClose={handleCloseCat}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
        >
            <Box className="bg-gray-50 rounded" sx={style}>
                <Typography className="text-center" id="modal-modal-title" variant="h6" component="h2">
                    {title}
                </Typography>
                <Typography className="flex justify-center" id="modal-modal-description" component="span" sx={{ mt: 2 }}>
                    <Tree  setCategory={setCategory}/>
                </Typography>
                <Typography className="flex justify-center items-end h-20">
                    <button onClick={sendCategory} className="bg-green-500 text-white p-2 rounded h-12 ">
                        Enviar
                    </button>
                    <button onClick={handleCloseCat} className="bg-red-500 text-white p-2 rounded h-12 ml-8">
                        Cerrar
                    </button>
                </Typography>
            </Box>
        </ModalMUI>
    );
}
