import * as React from "react";
import { DataGrid } from "@mui/x-data-grid";
import CheckIcon from '@mui/icons-material/Check';
import CloseIcon from '@mui/icons-material/Close';
import NumberFormat from "react-number-format";
import Loader from "../components/Loader";
import axios from "axios";

export default function NewTable({ handleModalCV, setPeso, handleModalCat, setSelectionModel, selectionModel, isLoading, setIsLoading }) {
    const [actualpage, setActualPage] = React.useState(1);
    const [pagesize, setPageSize] = React.useState(30);
    const [data, setData] = React.useState([]);
    const [totalpages, setTotalPages] = React.useState(0);
    const [newarray, setNewArray] = React.useState([1]);

    const localizedTextsMap = {
        columnMenuUnsort: "Sin Ordenar",
        columnMenuSortAsc: "Ordenar por orden creciente",
        columnMenuSortDesc: "Ordenar por orden descendente",
        columnMenuFilter: "Filtro",
        columnMenuHideColumn: "Ocultar",
        columnMenuShowColumns: "Mostrar columnas"};
    const rows = [];
    React.useEffect(() => {
        const getReponse = async () => {
            setIsLoading(true)
            const { data: response } = await axios.post("http://18.212.211.52:3000/api/get-data");
            setData([...rows, ...response.records])
            setTotalPages(response.total)
            setIsLoading(false)
        }
        getReponse();
        
    }, []);
    
    data.forEach((product) => {
        rows.push({
            id: product.cdt_sku,
            cdt_sku: product.cdt_sku,
            cdt_request_url: product.cdt_request_url,
            cdt_newimg: product.cdt_newimg,
            cdt_title: product.cdt_title,
            cdt_list_price: product.cdt_list_price,
            cdt_our_price: product.cdt_our_price,
            cdt_cost_price: product.precio_costo,
            cdt_applyvalue: product.cdt_applyvalue,
            cdt_tipocosto: product.cdt_tipocosto,
            cdt_precio_costo: product.precio_costo,
            cdt_status: product.listo,
            cdt_peso: product.cdt_peso,
            cdt_category: product.cdt_newcategoria,
        });
    });
    
    const columns = [
        { field: "cdt_sku", headerName: "SKU", width: 150 },
        {
            field: "cdt_newimg",
            headerName: "Imagen",
            width: 110,
            editable: false,
            renderCell: (params) => (
              <a className="mx-auto cursor-pointer" href={params.row.cdt_request_url} target="_blank" rel="noreferrer">
                  <img className="object-contain w-16 h-18" src={params.value} alt={params.row.cdt_id}/>
              </a>
          ),
        },
        {
            field: "cdt_title",
            headerName: "Titulo",
            width: 200,
            editable: false,
        },
        {
            field: "cdt_list_price",
            headerName: "Precio de lista",
            type: "number",
            width: 110,
            editable: false,
            renderCell: (params) => (
              <NumberFormat
              className=""
              value={Math.round(
                  params.row.cdt_list_price
              )}
              displayType="text"
              prefix="$"
              thousandSeparator="."
              decimalSeparator=","
          />
          ),
        },
        {
            field: "cdt_our_price",
            headerName: "Nuestro precio",
            type: "number",
            width: 110,
            editable: false,
            renderCell: (params) => (
              <NumberFormat
              className=""
              value={Math.round(
                  params.row.cdt_our_price
              )}
              displayType="text"
              prefix="$"
              thousandSeparator="."
              decimalSeparator=","
          />
          ),
        },
        {
            field: "cdt_precio_costo",
            headerName: "Costo Venta",
            type: "number",
            width: 110,
            editable: false,
            renderCell: (params) => (
              <NumberFormat
              className=""
              value={Math.round(
                  params.row.cdt_precio_costo
              )}
              displayType="text"
              prefix="$"
              thousandSeparator="."
              decimalSeparator=","
          />
          ),
        },
        {
            field: "cdt_tipocosto",
            headerName: "Tipo Costo",
            type: "number",
            width: 110,
            editable: false,
        },
        {
            field: "cdt_applyvalue",
            headerName: "Margen",
            type: "number",
            width: 110,
            editable: true,
        },
        {
            field: "cdt_peso",
            headerName: "Peso",
            type: "number",
            width: 110,
            editable: true,
        },
        {
          field: "cdt_category",
          headerName: "Categoria",
          type: "number",
          width: 110,
          editable: false,
      },
      {
          field: "cdt_status",
          headerName: "Status",
          type: "number",
          width: 110,
          editable: false,
          renderCell: (params) => (
              params.row.cdt_status ? <CheckIcon /> : <CloseIcon />
              ),
            },
        ];

    const handlePagination = async (params) => {
        if (params === 0) return;
        const getResponse = async () => {
            setIsLoading(true)
            let page = params + 1;
            
            const finded = newarray.some((x) => x === page || x === 0);
            if(!finded) {
                setActualPage(page);
            } else {
                setActualPage(page - 1);
            }
            console.log(actualpage)
            const { data: response } = await axios.post("http://18.212.211.52:3000/api/get-data", {
                actualpage
            });
            const result = await response;
            setData([...new Set([...rows, ...response.records])])
            
            setNewArray([...new Set([...newarray, page])])
            console.log(newarray);
            setIsLoading(false)
        }
        getResponse();  
    }
    
    return (
        <>
        <div>
                          { selectionModel.length > 0 ? (
                <div className="flex justify-end items-center w-full py-2 mb-4">
                <button
                            onClick={handleModalCV}
                            className="btn bg-green-500 hover:bg-green-600 text-white p-2 mx-2 mt-2 rounded w-18"
                        >
                            Margen Venta
                        </button>
                        <button
                            onClick={() => setPeso(true)}
                            className="btn bg-yellow-500 hover:bg-yellow-600 text-white p-2 mx-2 mt-2 rounded w-18"
                        >
                            Actualizar Peso
                        </button>
                        {/* <button
                            onClick={() => handleModalDsc(true)}
                            className="bg-gray-500 text-white p-2 mx-2 mt-2 rounded w-18"
                            disabled
                        >
                            % Descuento
                        </button> */}
    
                        <button
                            onClick={() => handleModalCat(true)}
                            className="btn bg-orange-500 hover:bg-orange-600 text-white p-2 mx-2 mt-2 rounded w-18"
                        >
                            Categorias
                        </button>
                            </div>
              ) :null }
        </div>
        <div className="w-full h-[40rem] shadow-xl border border-gray-400 rounded ">
                <DataGrid
                rows={rows}
                columns={columns}
                paginationMode="client"
                pageSize={pagesize} 
                // onPageSizeChange={(newPageSize) => setPageSize(newPageSize)}
                // onCellDoubleClick={(state) => setUpdatedState(state)} 
                disableSelectionOnClick
                rowCount={Math.ceil(totalpages)}
                rowsPerPageOptions={[5, 10, 30, 50]}
                checkboxSelection
                loading={isLoading}
                selectionModel={selectionModel}
                initialState={{
                    ...data.initialState,
                    pagination: {
                      pageSize: 25,
                    },
                  }}

                pagination
                onPageChange={handlePagination}
                // onCellEditCommit={handleCellCommit}
                onSelectionModelChange={(ids) => {
                  setSelectionModel(ids);
                }}
                localeText={localizedTextsMap}
                hideFooterPagination={isLoading}
            />
        </div>
        </>
    );
}
