import React, { useState } from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import ModalMUI from "@mui/material/Modal";

import axios from "axios";

const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    height: 220,
    boxShadow: 24,
};

export default function Modal({ title, open, handleClose , selectionModel, setIsLoading }) {
    const [margin, setMargin] = useState("")
    
    const sendData = () => {
        setIsLoading(true)
        axios.post("http://18.212.211.52:3000/api/update-pricecv", {margin, 
        selectionModel})
            .then((response) => {
            setIsLoading(true)
            handleClose()
            setIsLoading(false)
        })
    }
    return (
        <ModalMUI
            open={open}
            onClose={handleClose}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
        >
            <Box className="bg-gray-50 flex flex-col justify-around items-center rounded" sx={style}>
                <Typography id="modal-modal-title" variant="h6" component="h2">
                    {title}
                </Typography>
                <Typography id="modal-modal-description"component={'span'}  sx={{ mt: 2 }}>
                    <div>
                    %{" "}
                    <input
                        className="border rounded px-2 py-1"
                        type="number"
                        placeholder="Margen de venta..."
                        value={margin}
                        onChange={(e) => setMargin(e.target.value)}
                        required
                    />

                    </div>
                </Typography>
                <Typography className="flex justify-around w-44">
                    <button onClick={sendData} className="bg-green-500 hover:bg-green-600 text-white p-2 rounded ml-4">
                        Aplicar
                    </button>
                    <button onClick={handleClose} className="bg-red-500 text-white p-2 rounded">
                        Cerrar
                    </button>
                </Typography>
            </Box>
        </ModalMUI>
    );
}
