import React, { useEffect, useState } from "react";
import Table from "../components/Table";
import Modal from "../components/Modal";
import axios from "axios";

import ModalCategory from "../components/ModalCategory";
import ModalDescuento from "../components/ModalDescuento";
import ModalPeso from "../components/ModalPeso";

import Loader from "../components/Loader";
import NewTable from "../components/NewTable";

export default function Info() {
    const [loading, setLoading] = useState(false);
    const [refreshData, setRefreshData] = useState(false);

    const [open, setOpen] = useState(false);
    const [opendsc, setOpenDsc] = useState(false);
    const [opencategory, setOpenCategory] = useState(false);
    const [openpeso, setPeso] = useState(false)

    const [getGroup, setGetGroup] = useState([]);
    const [margin, setMargin] = useState(0);
    const [totalPages, setTotalPages] = useState(0);
    const [selectionModel, setSelectionModel] = React.useState([]);
    const [isLoading, setIsLoading] = React.useState(false);

    console.log(isLoading)



    const handleRefresh = () => setRefreshData(!refreshData);
    const handleCloseCV = () => setOpen(false);
    const handleClosePeso = () => setPeso(false);
    const handleCloseDsc = () => setOpenDsc(false);
    const handleCloseCat = () => setOpenCategory(false);
    const handleModalCV = () => {
        setOpen(true);
        setMargin(0);
    };
    const handleModalCat = () => {
        setOpenCategory(true);
    };
    return (
        <>
            {loading ? (
                <Loader />
            ) : (
                <div className="w-full p-4 bg-slate-200">
                    <Modal
                        title="Margen de venta"
                        open={open}
                        setOpen={setOpen}
                        margin={margin}
                        handleClose={handleCloseCV}
                        selectionModel={selectionModel}
                        setSelectionModel={setSelectionModel}
                        handleRefresh={handleRefresh}
                        setIsLoading={setIsLoading}
                    />
                    <ModalDescuento
                        title="Precio de descuento"
                        opendsc={opendsc}
                        handleCloseDsc={handleCloseDsc}
                    />

                    <ModalCategory 
                        title="Categorias"  
                        opencat={opencategory}
                        handleCloseCat={handleCloseCat}
                        selectionModel={selectionModel}
                        handleRefresh={handleRefresh}

                    />

                    <ModalPeso 
                        title="Actualizar o definir el peso"
                        openpeso={openpeso}
                        handleClosePeso={handleClosePeso}
                        selectionModel={selectionModel}
                        setRefreshData={setRefreshData}
                        refreshData={refreshData}
                        setGetGroup={setGetGroup}
                        isLoading={isLoading}
                        setIsLoading={setIsLoading}
                    />
                    <NewTable 
                        totalPages={totalPages}
                        handleModalCV={handleModalCV}
                        setPeso={setPeso} 
                        handleModalCat={handleModalCat}
                        selectionModel={selectionModel}
                        setSelectionModel={setSelectionModel}
                        setIsLoading={setIsLoading}
                        isLoading={isLoading}
                    />
                </div>
            )}
        </>
    );
}
