import React from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import ModalMUI from "@mui/material/Modal";

const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 600,
    height: 350,
    boxShadow: 24,
    p: 4,
};




export default function Modal({ title, opendsc, sendData, margin, setMargin, handleCloseDsc }) {
    return (
        <ModalMUI
            open={opendsc}
            onClose={handleCloseDsc}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
        >
            <Box className="bg-gray-50 flex flex-col justify-around items-center rounded" sx={style}>
                <Typography id="modal-modal-title" variant="h6" component="h2">
                    {title}
                </Typography>
                <Typography id="modal-modal-description" component="span" sx={{ mt: 2 }}>
                    <div>
                    %{" "}
                    <input
                        className="border rounded px-2 py-1"
                        type="text"
                        placeholder="Margen de venta..."
                        value={margin}
                        onChange={(e) => setMargin(e.target.value)}
                        required
                    />
                    <button onClick={sendData} className="bg-green-500 hover:bg-green-600 text-white p-1 rounded ml-4">
                        Aplicar
                    </button>
                    </div>
                </Typography>
                <Typography className="flex justify-end">
                    <button onClick={handleCloseDsc} className="bg-red-500 text-white p-2 rounded">
                        Cerrar
                    </button>
                </Typography>
            </Box>
        </ModalMUI>
    );
}
