import React, { useState } from "react";
import SettingsIcon from "@mui/icons-material/Settings";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";

import { Link } from "react-router-dom";
// import { useAuth0 } from "@auth0/auth0-react";

export default function Navbar() {
    // const { logout, user } = useAuth0();
    const [anchorEl, setAnchorEl] = useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };
    return (
        <div className="flex justify-between shadow-xl items-center px-6 bg-slate-900 h-[6rem] text-white">
            <div className=" w-11/12">
                <Link className="flex items-center" to="/">
                    <img
                        src="https://cdn.discordapp.com/attachments/961073006015291392/977315751818035250/png-clipart-phoenix-logo-design-mark-phoenix-removebg-preview.png"
                        alt="aveFenix"
                        className="w-8 h-8 m-3 mb-3"
                    />
                    <h1 className="font-bold text-lg">AveFenix Dashboard</h1>
                </Link>
            </div>
            <div className="flex justify-around items-center w-40">
                <div>
                    <SettingsIcon
                        id="basic-button"
                        aria-controls={open ? "basic-menu" : undefined}
                        aria-haspopup="true"
                        aria-expanded={open ? "true" : undefined}
                        onClick={handleClick}
                    />
                    <Menu
                        id="basic-menu"
                        anchorEl={anchorEl}
                        open={open}
                        onClose={handleClose}
                        MenuListProps={{
                            "aria-labelledby": "basic-button",
                        }}
                    >
                        <MenuItem onClick={handleClose}>Profile</MenuItem>
                        <MenuItem onClick={handleClose}>My account</MenuItem>
                        <MenuItem onClick={handleClose}>Logout</MenuItem>
                    </Menu>
                </div>
                <button>
                    <svg
                        stroke="currentColor"
                        fill="currentColor"
                        strokeWidth="0"
                        viewBox="0 0 512 512"
                        className="w-5 h-5"
                        aria-hidden="true"
                        height="1em"
                        width="1em"
                        xmlns="http://www.w3.org/2000/svg"
                    >
                        <path d="M256 480a80.09 80.09 0 0073.3-48H182.7a80.09 80.09 0 0073.3 48zm144-192v-60.53C400 157 372.64 95.61 304 80l-8-48h-80l-8 48c-68.88 15.61-96 76.76-96 147.47V288l-48 64v48h384v-48z"></path>
                    </svg>
                </button>
                <button className="w-8 h-8 border-2 rounded-full bg-white border-green-400">
                    <img
                        src="https://cdn.discordapp.com/attachments/961073006015291392/977315751818035250/png-clipart-phoenix-logo-design-mark-phoenix-removebg-preview.png"
                        alt="imgPerfil"
                        className="object-cover rounded-full"
                    />
                </button>
                <span></span>
            </div>
        </div>
    );
}
